class HomeGrid extends React.Component {
    render() {
        return <Container>
                <Row noGutters="true">
                    <Col >
                        <HomeTitleCard />
                        <ExperienceCard />
                        <EducationCard />
                    </Col>
                </Row>
            </Container>
    }
}

class Home extends React.Component {
    render() {
        return <div>
                <HomeGrid />
               </div>
    }
}

export default Home;